.icon-button {
  height: 80px;
  width: 80px;
  background-color: white;
  opacity: 30%;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.icon-button:hover {
  opacity: 70%;
}

.icon-button-icon {
  height: 60%;
  width: 60%;
}

.disabled {
  opacity: 10% !important;
}

.disabled:hover {
  opacity: 10% !important;
  cursor: default !important;
}
